import React from 'react';
import ButtonGroup from 'components/ButtonGroup';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
  PointText,
} from 'components/BaseStyles';
import styled, { css } from 'styled-components';
import ReadOnlyBasket from 'components/ReadOnlyBasket';
import { pointViews, polygonsGroupByCluster, scores } from 'App/datas';
import { Theme } from 'styles/theme';
import { ColorType } from 'styles/colors';

const StyledContent = styled(Content)`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 60%;
  height: fit-content;
  margin: auto;
  margin-top: 7rem;
`;

const Question = styled.div`
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
`;
const Txt = styled.div`
  font-size: 1.1rem;
  height: 2rem;
`;

const Highlighting = styled.span`
  ${({ theme }) => css`
    color: ${(theme as Theme).palette[ColorType.Blue]};
  `}
`;

const AnalyticsProgress: React.FC = () => {
  const { groupScores, pointSum, score } = scores[2];
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>종합 분석 과정</ContentTitle>
          <ExplainBox>
            데이터가 잘 정리되었다는 것은 비슷한 것끼리 묶여 있다는 의미야.
            <br />
            그룹 내에 크기, 색깔, 모양 각 관점으로 같은 도형이 몇 번 나타났을까?
          </ExplainBox>
          <StyledContent>
            {pointViews.map((pointView) => {
              const maxLengthPolygonIds = groupScores[pointView.groupBy];
              return (
                <div key={pointView.title}>
                  <Question>
                    같은 <PointText>{pointView.title}</PointText> 도형은 최대{' '}
                    <Highlighting>{maxLengthPolygonIds.length}</Highlighting>개
                  </Question>
                  <ReadOnlyBasket
                    group={polygonsGroupByCluster[2]}
                    showBorders={maxLengthPolygonIds}
                  />
                </div>
              );
            })}
          </StyledContent>
          <StyledContent
            style={{
              background: '#FAFAFA',
              marginTop: '1rem',
              fontSize: '1rem',
              padding: '1em',
              textAlign: 'center',
              display: 'block',
            }}
          >
            <Txt>
              한 그룹에서 같은 크기는 {groupScores.size.length}개, 같은 색깔은{' '}
              {groupScores.color.length}개, 모양은 {groupScores.type.length}개가
              있어. 모두를 더하면 {pointSum}이(가) 되는구나.
            </Txt>
            <Txt>
              다른 그룹과의 비교를 위해 총 도형 수로 나누기{' '}
              {polygonsGroupByCluster[2].length} 연산을 하니,
            </Txt>
            <Txt>
              첫 번째 그룹에 있는 도형의 유사도 점수는 {score.toFixed(2)} 로
              나오네!
            </Txt>
          </StyledContent>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default AnalyticsProgress;
