import styled, { css } from 'styled-components';

export const Circle = styled.div`
  border-radius: 50%;
  padding: 1rem;
  border: 1px solid gray;
  background-color: #fff;
  height: 8rem;
  width: 8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Yspot';
  font-size: 1.4rem;
`;
export const Explain = styled.div`
  margin-left: 2rem;
  line-height: 1.8rem;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 1rem;
`;
export const ColorBox = styled.div`
  border: 1px solid #dcdcdc;
  box-shadow: 5px 5px 5px #dcdcdc;
  border-radius: 1rem;
  width: 13rem;
  height: 16rem;
  &:not(:last-child) {
    margin-right: 1rem;
  }
`;
export const Flex = styled.div`
  display: flex;
  justify-content: center;
  margin: 1rem 0;
  padding: 1rem;
  border-radius: 1rem;
  background-color: #ececec;
  width: 70rem;
`;
export const GroupDiv = styled.div`
  display: flex;
  justify-content: center;
`;

type Props = {
  bgColor: string;
};
export const GroupTitle = styled.div<Props>`
  ${(props) =>
    css`
      background-color: ${props.bgColor};
    `}
  border-top-left-radius: 0.95rem;
  border-top-right-radius: 0.95rem;
  font-family: 'Yj';
  padding: 0.5rem;
  color: #fff;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
`;
export const InBoxTitle = styled.div`
  font-weight: 800;
`;
export const InBoxContent = styled.div`
  font-size: 1rem;
  color: gray;
`;
export const FlexBothCenter = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  padding: 2rem 1rem;
  height: 13rem;
`;
