import React, { useContext } from 'react';
import styled from 'styled-components';
import AppContext, { centerBasketSize, GroupType } from 'App/context';
import { polygons } from 'App/datas';
import Polygon from './Polygon';

const Grid = styled.div`
  position: absolute;
  background: #f2f2f2;
  border-radius: 1rem;
  padding: 1rem;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 5px;
  z-index: 999;
`;

const Block = styled.div`
  width: 3vw;
  height: 3vw;
`;

const CenterBasket: React.FC = () => {
  const {
    state: { groups },
  } = useContext(AppContext);
  const groupType = GroupType.Center;
  const group = groups[groupType];

  return (
    <Grid>
      {Array.from(Array(centerBasketSize).keys()).map((index) => {
        const polygon = polygons[group[index]];
        if (polygon !== undefined) {
          const { id: key, ...rest } = polygon;
          return <Polygon key={key} id={key} from={groupType} {...rest} />;
        }
        return <Block key={centerBasketSize + index} />;
      })}
    </Grid>
  );
};

export default CenterBasket;
