import React, { useState } from 'react';
import Cover from 'pages/Cover';
import DragAndDrop from 'pages/DragAndDrop';
import Survey from 'pages/Survey';
import CompareBySize from 'pages/CompareBySize';
import CompareByColor from 'pages/CompareByColor';
import CompareByShape from 'pages/CompareByShape';
import General from 'pages/General';
import ExperienceDone from 'pages/ExperienceDone';
import ResultCompare from 'pages/ResultCompare';
import FindOut from 'pages/FindOut';
import WhatIs from 'pages/WhatIs';
import Purpose from 'pages/Purpose';
import Understanding from 'pages/Understanding';
import UseCase1 from 'pages/UseCase1';
import UseCase2 from 'pages/UseCase2';
import UnderstandingComplete from 'pages/UnderstandingComplete';
import AnalyticsProgress1 from 'pages/AnalyticsProgress1';
import AnalyticsProgress2 from 'pages/AnalyticsProgress2';
import AnalyticsResult from 'pages/AnalyticsResult';
import MyAnalyticsResult from 'pages/MyAnalyticsResult';
import AppContext, {
  defaultState,
  GroupType,
  minPage,
  StateType,
} from './context';

const pages = [
  <Cover />,
  <DragAndDrop />,
  <CompareBySize />,
  <CompareByColor />,
  <CompareByShape />,
  <General />,
  <AnalyticsProgress1 />,
  <AnalyticsProgress2 />,
  <AnalyticsResult />,
  <MyAnalyticsResult groupId={0} />,
  <MyAnalyticsResult groupId={1} />,
  <MyAnalyticsResult groupId={2} />,
  <MyAnalyticsResult groupId={3} />,
  <ResultCompare />,
  <ExperienceDone />,
  <FindOut />,
  <WhatIs />,
  <Purpose />,
  <Understanding />,
  <UseCase1 />,
  <UseCase2 />,
  <UnderstandingComplete />,
  <Survey />,
];
const maxStage = pages.length - 1;

const App: React.FC = () => {
  const [state, setState] = useState<StateType>(defaultState);
  const goToHome = () => setState(defaultState);
  const handleGoToPage = (addValue: number) => () => {
    setState((prevState) => {
      const newStage = prevState.page + addValue;
      const page =
        minPage <= newStage && newStage <= maxStage ? newStage : minPage;
      return {
        ...prevState,
        page,
      };
    });
  };
  const goToPrev = handleGoToPage(-1);
  const goToNext = handleGoToPage(1);
  const moveGroup = (id: number, from: GroupType, to: GroupType) => {
    if (from === to) return;

    setState((prevState) => ({
      ...prevState,
      groups: {
        ...prevState.groups,
        [from]: prevState.groups[from].filter((polygonId) => polygonId !== id),
        [to]: prevState.groups[to].concat(id),
      },
    }));
  };
  return (
    <AppContext.Provider
      value={{
        state,
        setState,
        goToHome,
        goToPrev,
        goToNext,
        moveGroup,
      }}
    >
      {pages[state.page]}
    </AppContext.Provider>
  );
};

export default App;
