import React, { useContext } from 'react';
import styled from 'styled-components';
import AppContext from 'App/context';
import BasePage from 'components/BasePage';
import { Container } from 'components/BaseStyles';
import { Bounce } from 'styles/animations';
import BookRobot from 'assets/images/book_robot.png';

const Title = styled.div`
  font-size: 4rem;
  color: #fff;
  font-family: 'Yj', sans-serif;
`;
const Button = styled.button`
  padding: 1rem 5rem;
  color: #fff;
  background-color: #165be5;
  outline: 0;
  border: 0;
  font-size: 1.8rem;
  font-family: 'Yspot', sans-serif;
  border-radius: 1rem;
  cursor: pointer;
  animation: ${Bounce} 1s infinite alternate;
`;
const ButtonDiv = styled.div`
  position: absolute;
  bottom: 2rem;
`;
const Content = styled.div`
  color: #fff;
  font-size: 1.6rem;
  text-align: center;
  margin-top: 3.5rem;
  font-family: 'Score';
  line-height: 2.2rem;
`;
const ImgDiv = styled.div`
  position: absolute;
  right: 5rem;
  bottom: 5rem;
`;
const Img = styled.img`
  width: 20rem;
`;

const Compare1: React.FC = () => {
  const { goToNext } = useContext(AppContext);
  return (
    <BasePage>
      <Container style={{ backgroundColor: '#3AA9EA' }}>
        <Title>클러스터링 기법 알아보기</Title>
        <Content>
          데이터를 직접 정리해보니 어때? <br />
          나의 직관과 실제 분석된 결과의 차이는 어디서 발생하는 것일까?
          <br />
          클러스터링 기법은 무엇이며, 사용하면 어떤 점이 좋은지 함께 알아보자!
        </Content>
        <ImgDiv>
          <Img src={BookRobot} alt="" />
        </ImgDiv>
        <ButtonDiv>
          <Button onClick={goToNext}>알아보기</Button>
        </ButtonDiv>
      </Container>
    </BasePage>
  );
};

export default Compare1;
