import React from 'react';
import styled, { css } from 'styled-components';
import { darken, lighten } from 'polished';
import { Theme } from '../styles/theme';
import { ColorType } from '../styles/colors';

type SizeType = 'xxlarge' | 'xlarge' | 'large' | 'medium' | 'small';
type PaddingType = 'longWidth' | 'large' | 'medium';
type StyledButtonProps = {
  theme: Theme;
  color: ColorType;
  size: SizeType;
  padding: PaddingType;
  fullWidth: boolean;
  outline: boolean;
};

const colorStyles = css`
  ${({ theme, color }: StyledButtonProps) => {
    const selected = theme.palette[color];
    return css`
      background: ${selected};
      &:hover {
        background: ${lighten(0.1, selected)};
      }
      &:active {
        background: ${darken(0.1, selected)};
      }
      &:disabled {
        background: ${lighten(0.4, selected)};
        cursor: not-allowed;
      }
      ${(props: StyledButtonProps) =>
        props.outline &&
        css`
          color: ${selected};
          background: none;
          border: 1px solid ${selected};
          &:hover {
            background: ${selected};
            color: white;
          }
          &:active {
            background: ${darken(0.1, selected)};
          }
        `}
    `;
  }}
`;

const sizes = {
  xxlarge: {
    height: '3.75rem',
    fontSize: '3rem',
  },
  xlarge: {
    height: '3.5rem',
    fontSize: '2rem',
  },
  large: {
    height: '3rem',
    fontSize: '1.25rem',
  },
  medium: {
    height: '2.25rem',
    fontSize: '1rem',
  },
  small: {
    height: '1.75rem',
    fontSize: '0.875rem',
  },
};
const paddings = {
  longWidth: {
    padding: '1.5rem 4rem',
  },
  medium: {
    padding: '1rem',
  },
  large: {
    padding: '3rem 4rem',
  },
};

const sizeStyles = css`
  ${({ size }: StyledButtonProps) =>
    css`
      height: ${sizes[size].height};
      font-size: ${sizes[size].fontSize};
    `}
`;

const paddingStyles = css`
  ${({ padding }: StyledButtonProps) =>
    css`
      padding: ${paddings[padding].padding};
    `}
`;

const fullWidthStyle = css`
  ${(props: StyledButtonProps) =>
    props.fullWidth &&
    css`
      width: 100%;
      justify-content: center;
      &:not(:first-child) {
        margin-left: 0;
        margin-top: 1rem;
      }
    `}
`;

const StyledButton = styled.button<StyledButtonProps>`
  /* common style */
  display: inline-flex;
  outline: none;
  border: none;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  align-items: center;
  font-family: 'Yspot';
  /* etc */
  &:not(:first-child) {
    margin-left: 1rem;
  }

  /* size */
  ${sizeStyles}

  /* color */
  ${colorStyles}

  /* width */
  ${fullWidthStyle}

  /* padding */
  ${paddingStyles}
`;

type ButtonProps = {
  color?: ColorType;
  size?: SizeType;
  outline?: boolean;
  padding?: PaddingType;
  fullWidth?: boolean;
  disabled?: boolean;
  onClick?: () => void;
};

const Button: React.FC<ButtonProps> = ({
  children,
  color = ColorType.Blue,
  size = 'medium',
  padding = 'medium',
  outline = false,
  fullWidth = false,
  ...rest
}) => {
  return (
    <StyledButton
      color={color}
      size={size}
      padding={padding}
      outline={outline}
      fullWidth={fullWidth}
      {...rest}
    >
      {children}
    </StyledButton>
  );
};

export default Button;
