import React, { useContext } from 'react';
import PointText from 'components/PointText';
import BasePage from 'components/BasePage';
import ButtonGroup from 'components/ButtonGroup';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import ReadOnlyBasket from 'components/ReadOnlyBasket';
import { polygonsGroupBySize } from 'App/datas';
import styled from 'styled-components';
import AppContext, { GroupType } from 'App/context';

const GridByTwoColumn = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 0.5vw;
  max-width: 40vw;
  width: fit-content;
  height: fit-content;
`;

const CompareContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;

  .my {
    margin-left: 10vw;
    opacity: 0.5;
  }
`;

const CompareBySize: React.FC = () => {
  const groupsResultByComputer = Object.entries(polygonsGroupBySize);
  const {
    state: { groups },
  } = useContext(AppContext);
  const groupsResultByMe = Object.entries(groups).filter(
    ([key]) => ((key as unknown) as string) !== GroupType.Center.toString(),
  );
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>데이터 분석 관점 비교 ①</ContentTitle>
          <ExplainBox>
            <PointText>크기</PointText>로 나눌 수 있겠네. 나의 정리와
            비교해볼까?
          </ExplainBox>
          <Content>
            <CompareContainer>
              <div>
                <h2>크기로 정리</h2>
                <GridByTwoColumn>
                  {groupsResultByComputer.map(([key, group]) => (
                    <ReadOnlyBasket key={key} group={group} />
                  ))}
                </GridByTwoColumn>
              </div>
              <div className="my">
                <h2>나의 정리</h2>
                <GridByTwoColumn>
                  {groupsResultByMe.map(([key, group]) => (
                    <ReadOnlyBasket key={key} group={group} />
                  ))}
                </GridByTwoColumn>
              </div>
            </CompareContainer>
          </Content>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default CompareBySize;
