import React from 'react';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import styled, { css } from 'styled-components';
import { basketSize, centerBasketSize } from 'App/context';
import { polygons } from 'App/datas';
import { Theme } from 'styles/theme';
import { ColorType } from 'styles/colors';
import Polygon from './Polygon';

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 0.5vw;
  padding: 0.5vw;
  margin: auto;
  width: fit-content;
  height: fit-content;
  ${({ theme }) => css`
    background: ${(theme as Theme).palette[ColorType.LightGray]};
  `}
`;

const Block = styled.div`
  width: 3vw;
  height: 3vw;
`;

const ReadOnlyBasket: React.FC<{ group: number[]; showBorders?: number[] }> = ({
  group,
  showBorders = [],
}) => {
  return (
    <DndProvider backend={HTML5Backend}>
      <Grid>
        {Array.from(Array(basketSize).keys()).map((index) => {
          const polygon = polygons[group[index]];
          if (polygon !== undefined) {
            const { id: key, ...rest } = polygon;
            return (
              <Polygon
                key={key}
                id={key}
                canDrag={false}
                showBorder={showBorders.includes(key)}
                {...rest}
              />
            );
          }
          return <Block key={centerBasketSize + index} />;
        })}
      </Grid>
    </DndProvider>
  );
};

export default ReadOnlyBasket;
