import React from 'react';
import styled from 'styled-components';

type Props = {
  title?: string;
  content?: string;
};

const Div = styled.div`
  position: absolute;
  bottom: 1.5rem;
  left: 1.5rem;
  box-shadow: 1px 0 8px 0 rgba(0, 0, 0, 0.16);
  background-color: #fbfbfb;
  padding: 1.5rem;
  max-width: 25rem;
  border-radius: 1rem;
  z-index: 1;
`;
const Title = styled.div`
  font-size: 1.1rem;
  font-weight: 800;
  margin-bottom: 1rem;
`;

const HelpBox: React.FC<Props> = ({ children, title, content }) => {
  return (
    <Div>
      {title && <Title>{title}</Title>}
      <div>
        {content} {children}
      </div>
    </Div>
  );
};

export default HelpBox;
