import React from 'react';
import styled from 'styled-components';
import PointText from 'components/PointText';
import ButtonGroup from 'components/ButtonGroup';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import DimensionImg from 'assets/images/dimension.gif';

const Img = styled.img`
  margin-top: 2.5rem;
`;

const ExperienceDone: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>데이터 정리 체험 완료!</ContentTitle>
          <ExplainBox>
            <div>
              지금까지 잘 따라왔어! 이처럼 데이터의 다양한 특성을 종합하여,
              <br />
              기계가 유사한 데이터끼리 묶는 것을{' '}
              <PointText>클러스터링</PointText>
              이라고 해. <br />
              다음 장부터 클러스터링 분석기법에 대해 같이 알아보자.
            </div>
          </ExplainBox>
          <Content>
            <Img src={DimensionImg} alt="" />
          </Content>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default ExperienceDone;
