import React from 'react';
import styled from 'styled-components';
import BasePage from 'components/BasePage';
import PointText from 'components/PointText';
import ButtonGroup from 'components/ButtonGroup';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
  FlexDiv,
  Img,
} from 'components/BaseStyles';
import Graph1 from 'assets/images/graph_all.png';

const ImgDiv = styled.div`
  width: 28rem;
  display: flex;
  flex-wrap: wrap;
  img {
    width: 24rem;
  }
`;

const UseCase2: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>클러스터링 분석 활용 사례 ②</ContentTitle>
          <ExplainBox>
            클러스터링 분석은 시장을 세분화하는데 가장 널리 쓰이는 방법 중
            하나야.
            <br />
            공공정책 부문에서도 맞춤형 정책을 펼치기 위해 활용되고 있어.
          </ExplainBox>
          <Content style={{ marginTop: '2.5rem' }}>
            <FlexDiv>
              <ImgDiv>
                <Img src={Graph1} alt="" />
              </ImgDiv>
              <div style={{ marginLeft: '2rem' }}>
                <div>
                  <PointText>출처 : 성남시 행복지수 연구 2020</PointText>
                </div>
                <div>
                  <br />
                  성남시 청소년 1,500명을 대상으로 실시한 설문조사에서 <br />
                  행복 관련 응답 유사성에 따라 5개의 집단으로 분류했어. <br />
                  <br />
                  1,500명은 5개의 집단으로 분류되었고 <br />
                  행복 취약 집단은 어떤 특성을 갖고 있는지 분석하여
                  <br />
                  오늘도 내일도 행복한 성남시 청소년을 위한
                  <br />
                  정책을 기획하고 집행하는데 사용되었어!
                </div>
              </div>
            </FlexDiv>
          </Content>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default UseCase2;
