import React, { useContext } from 'react';
import styled from 'styled-components';
import AppContext from '../App/context';
import Button from './Button';

const Div = styled.div`
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 0;
  padding: 1.5rem 0;
  width: 100%;
`;

type ButtonGroupProps = {
  isOpenNextPage?: boolean;
  lastPage?: boolean;
  buttonText?: string;
  callbackNext?: () => void;
};

const ButtonGroup: React.FC<ButtonGroupProps> = ({
  isOpenNextPage = true,
  callbackNext,
  lastPage,
  buttonText = '다음',
}) => {
  const { goToHome, goToNext } = useContext(AppContext);
  return (
    <Div>
      {lastPage ? (
        <Button
          size="xlarge"
          onClick={() => {
            if (callbackNext) {
              callbackNext();
            }
            setTimeout(goToHome, 3000);
          }}
          disabled={!isOpenNextPage}
        >
          설문 제출 후 처음으로
        </Button>
      ) : (
        <Button
          size="xlarge"
          padding="longWidth"
          onClick={() => {
            if (callbackNext) {
              callbackNext();
            }
            goToNext();
          }}
          disabled={!isOpenNextPage}
        >
          {buttonText}
        </Button>
      )}
    </Div>
  );
};

export default ButtonGroup;
