import React from 'react';
import ButtonGroup from 'components/ButtonGroup';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import styled, { css } from 'styled-components';
import ReadOnlyBasket from 'components/ReadOnlyBasket';
import { polygonsGroupByCluster, scores } from 'App/datas';
import { Theme } from 'styles/theme';
import { ColorType } from 'styles/colors';

const StyledContent = styled(Content)`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 80%;
  height: fit-content;
  margin: auto;
  margin-top: 3rem;
`;
const PointViewTitle = styled.div`
  font-size: 1.5rem;
  font-weight: 800;
  margin-bottom: 1rem;
`;

const Highlighting = styled.span`
  ${({ theme }) => css`
    color: ${(theme as Theme).palette[ColorType.Blue]};
  `}
`;

const AnalyticsResult: React.FC = () => {
  const totalScore = scores
    .map((scoreInfo) => scoreInfo.score)
    .reduce((prev, current) => prev + current, 0)
    .toFixed(2);
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>종합 분석 결과</ContentTitle>
          <ExplainBox>
            다른 그룹들도 이전 페이지와 동일한 방식으로 계산해봤어.
            <br />
            다양한 기준을 적용해서 4개 그룹을 분석해보니, {totalScore}점이
            나왔네!
            <br />
            그럼 같은 방식으로 나의 정리 결과도 확인해볼까?
          </ExplainBox>
          <StyledContent>
            {scores.map(({ cluster, score }) => {
              return (
                <div key={cluster}>
                  <h2>그룹{cluster + 1}</h2>
                  <ReadOnlyBasket group={polygonsGroupByCluster[cluster]} />
                  <p>{score.toFixed(2)}</p>
                </div>
              );
            })}
          </StyledContent>
          <StyledContent
            style={{
              marginTop: '1rem',
              fontSize: '2rem',
              padding: '1em',
              textAlign: 'center',
              display: 'block',
            }}
          >
            총점 <Highlighting>{totalScore}</Highlighting>
          </StyledContent>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default AnalyticsResult;
