import React, { useContext } from 'react';
import styled from 'styled-components';
import ButtonGroup from 'components/ButtonGroup';
import BasePage from 'components/BasePage';
import DndBoard from 'components/DndBoard';
import AppContext, { centerBasketSize, GroupType } from 'App/context';
import HelpBox from 'components/HelpBox';
import Button from 'components/Button';
import { ColorType } from 'styles/colors';

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
`;
const TopSection = styled.div`
  width: fit-content;
  margin: 0 auto;
  margin-top: 2rem;
  position: absolute;
  top: 0;
`;
const Title = styled.div`
  font-family: 'Yspot';
  font-size: 2.4rem;
  text-align: center;
  padding-top: 0.8rem;
`;
const Explain = styled.div`
  font-size: 1.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0 0.8rem 0;
  text-align: center;
`;

const DragAndDrop: React.FC = () => {
  const {
    state: { groups },
    setState,
  } = useContext(AppContext);
  const isEmptyCenter = groups[GroupType.Center].length === 0;
  const isExistNotAllowedGroup =
    [1, 2].includes(groups[GroupType.Group1].length) ||
    [1, 2].includes(groups[GroupType.Group2].length) ||
    [1, 2].includes(groups[GroupType.Group3].length) ||
    [1, 2].includes(groups[GroupType.Group4].length);
  const isOpenNextPage = isEmptyCenter && !isExistNotAllowedGroup;
  const isFullCenter = groups[GroupType.Center].length === centerBasketSize;
  return (
    <BasePage>
      <Container>
        <TopSection>
          <Title>뒤섞인 데이터를 정리해보자</Title>
          <Explain>
            4개의 바구니에 블럭을 정리해보자
            <br />
            어떻게 나눠야 할까?
          </Explain>
        </TopSection>
        <DndBoard />
        {isExistNotAllowedGroup && (
          <HelpBox content="한 그룹에 3개 이상의 도형이 있도록 데이터를 정리해줘!" />
        )}
        {isFullCenter && (
          <HelpBox title="드래그 해서 다른 그룹으로 이동해봐!">
            <div>점선으로 구분된 4개의 영역이 각 그룹이야.</div>
            <div>모든 그룹을 다 활용하지 않아도 분류할 수 있어.</div>
            <div>한 그룹에 최대 12개의 도형을 넣을 수 있어.</div>
            <div style={{ marginTop: '10px' }}>
              <Button
                color={ColorType.MidGray}
                onClick={() => {
                  const centerArr = [
                    ...groups[GroupType.Center].sort(() => 0.5 - Math.random()),
                  ];
                  const newGroups: number[][] = [[], [], [], []];
                  while (centerArr.length > 0) {
                    const pickedGroupIdx = Math.floor(
                      Math.random() * newGroups.length,
                    );
                    newGroups[pickedGroupIdx] = [
                      ...newGroups[pickedGroupIdx],
                      ...centerArr.splice(0, 1),
                    ];
                  }
                  setState((prevState) => ({
                    ...prevState,
                    groups: {
                      [GroupType.Center]: [],
                      [GroupType.Group1]: newGroups[0],
                      [GroupType.Group2]: newGroups[1],
                      [GroupType.Group3]: newGroups[2],
                      [GroupType.Group4]: newGroups[3],
                    },
                  }));
                }}
              >
                자동으로 랜덤 분류하기
              </Button>
            </div>
          </HelpBox>
        )}
        <ButtonGroup isOpenNextPage={isOpenNextPage} />
      </Container>
    </BasePage>
  );
};

export default DragAndDrop;
