export type GroupByFieldType = 'type' | 'size' | 'color' | 'cluster';
const groupBy = (data: PolygonProps[], field: GroupByFieldType) =>
  data.reduce<Record<string, number[]>>(
    (prev, current) => ({
      ...prev,
      [current[field]]: [...(prev[current[field]] || []), current.id],
    }),
    {},
  );

export const polygonsGroupByCluster = groupBy(polygons, 'cluster');
export const polygonsGroupBySize = groupBy(polygons, 'size');
export const polygonsGroupByColor = groupBy(polygons, 'color');
export const polygonsGroupByType = groupBy(polygons, 'type');

export const getClusterGroup: (
  data: number[],
  by: GroupByFieldType,
) => Record<string, number[]> = (data, by) =>
  groupBy(
    data.map((polygonId) => polygons[polygonId]),
    by,
  );

export const getMaxLengthArray: (data: number[][]) => number[] = (data) =>
  data.reduce(
    (prev, current) => (prev.length >= current.length ? prev : current),
    [],
  );

export const pointViews: { title: string; groupBy: GroupByFieldType }[] = [
  {
    title: '크기',
    groupBy: 'size',
  },
  {
    title: '컬러',
    groupBy: 'color',
  },
  {
    title: '모양',
    groupBy: 'type',
  },
];

export const getScores: (
  groups: number[][],
) => {
  cluster: number;
  groupScores: Record<GroupByFieldType, number[]>;
  pointSum: number;
  score: number;
}[] = (groups) =>
  groups.map((group, index) => {
    const groupScores = pointViews.reduce(
      (prev, current) => ({
        ...prev,
        [current.groupBy]: getMaxLengthArray(
          Object.values(getClusterGroup(group, current.groupBy)),
        ),
      }),
      {} as Record<GroupByFieldType, number[]>,
    );
    const pointSum = Object.values(groupScores)
      .map((polygonIds) => polygonIds.length)
      .reduce((prev, current) => prev + current, 0);
    const score = pointSum / group.length;
    return {
      cluster: index,
      groupScores,
      pointSum,
      score: Number.isNaN(score) ? 0 : score,
    };
  });

export const scores = getScores(
  clusters.map((cluster) => polygonsGroupByCluster[cluster]),
);
