import React, { useEffect, useState } from 'react';
import styled, { css } from 'styled-components';
import ChatBot from 'assets/images/chat_robot.png';
import { Opacity } from 'styles/animations';
import messenger from 'assets/sounds/messenger.mp3';

const audio = new Audio(messenger);

enum MessageStatus {
  HIDE,
  APPREARING,
  SHOW,
}

type ChatBubbleContainerProps = {
  isFirstPersonView?: boolean;
  messageStatus: MessageStatus;
};

const AvatarWrapper = styled.span`
  min-width: 4rem;
  min-height: 4rem;
  max-width: 4rem;
  max-height: 4rem;
  overflow: hidden;
`;

const Avatar = styled.img`
  width: 100%;
  height: 100%;
`;

const ChatMessage = styled.div`
  padding: 1em;
  font-size: 1.1rem;
  border-radius: 10px;
  background: #fff;
  max-width: 85%;
  text-align: left;
  height: 100%;
  line-height: 1.6rem;
`;

const switchMessageStatus = (status: MessageStatus) => {
  switch (status) {
    case MessageStatus.HIDE:
      return css`
        visibility: hidden;
      `;

    case MessageStatus.APPREARING:
      return css`
        animation: ${Opacity} 1s ease-out both;
      `;

    case MessageStatus.SHOW:
      break;

    default:
      break;
  }
  return css``;
};

const ChatBubbleContainer = styled.div<ChatBubbleContainerProps>`
  padding: 1rem;
  position: relative;
  display: flex;
  align-items: center;
  width: 80%;

  ${({ isFirstPersonView }) => css`
    align-self: ${isFirstPersonView ? 'flex-end' : 'flex-start'};
    flex-direction: ${isFirstPersonView ? 'row-reverse' : 'row'};
  `}

  ${({ messageStatus }) => switchMessageStatus(messageStatus)}

  & + & {
    margin-top: 0.25rem;
  }
`;

type ChatBubbleProps = {
  imgSrc: string;
  text: string;
} & ChatBubbleContainerProps;

const ChatBubble: React.FC<ChatBubbleProps> = ({
  imgSrc,
  text,
  isFirstPersonView,
  messageStatus,
}) => {
  return (
    <ChatBubbleContainer
      isFirstPersonView={isFirstPersonView}
      messageStatus={messageStatus}
    >
      <AvatarWrapper>
        <Avatar src={imgSrc} />
      </AvatarWrapper>
      <ChatMessage>{text}</ChatMessage>
    </ChatBubbleContainer>
  );
};

type ChatUIContainerProps = {
  backgroundImgSrc?: string;
};

const ChatUIContainer = styled.div<ChatUIContainerProps>`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;

  ${(props) =>
    props.backgroundImgSrc &&
    css`
      background-image: url(${props.backgroundImgSrc});
    `}
`;

type UserType = {
  userId: number;
  isFirstPersonView?: boolean;
  imgSrc?: string;
};

type MessageType = {
  id: number;
  userId: number;
  text: string;
};

type ChatUIProps = {
  users: UserType[];
  messages: MessageType[];
  animation?: boolean;
  setOpenNextPage?: React.Dispatch<React.SetStateAction<boolean>>;
} & ChatUIContainerProps;

const ChatUI: React.FC<ChatUIProps> = ({
  users,
  messages,
  backgroundImgSrc,
  animation,
  setOpenNextPage,
}) => {
  const [animationStep, setAnimationStep] = useState(0);
  useEffect(() => {
    if (animation) {
      if (animationStep < messages.length - 1) {
        if (animationStep === 0) {
          audio.play();
        }
        setTimeout(() => {
          setAnimationStep((step) => step + 1);
        }, 1500);
      } else if (setOpenNextPage && animationStep === messages.length - 1) {
        setOpenNextPage(true);
      }
    }
  }, [animation, animationStep, messages, setOpenNextPage]);
  const meUserId =
    users.find((user) => user.isFirstPersonView)?.userId || users[0].userId;
  const userMap = new Map(users.map((user) => [user.userId, user]));
  return (
    <ChatUIContainer backgroundImgSrc={backgroundImgSrc}>
      {messages.map((message) => {
        const user = userMap.get(message.userId);
        const src = user?.imgSrc || ChatBot;
        const isFirstPersonView = user?.userId === meUserId;
        let status = MessageStatus.SHOW;
        if (animation) {
          if (message.id === animationStep) {
            status = MessageStatus.APPREARING;
          } else if (message.id > animationStep) {
            status = MessageStatus.HIDE;
          }
        }
        return (
          <ChatBubble
            key={message.id}
            imgSrc={src}
            text={message.text}
            isFirstPersonView={isFirstPersonView}
            messageStatus={status}
          />
        );
      })}
    </ChatUIContainer>
  );
};

export default ChatUI;
