import React, { createContext } from 'react';

export const basketSize = 12;
export const centerBasketSize = basketSize * 2;

export enum GroupType {
  Center,
  Group1,
  Group2,
  Group3,
  Group4,
}

export type StateType = {
  page: number;
  groups: Record<GroupType, number[]>;
};

export type AppContextProps = {
  state: StateType;
  setState: React.Dispatch<React.SetStateAction<StateType>>;
  goToHome: () => void;
  goToPrev: () => void;
  goToNext: () => void;
  moveGroup: (id: number, from: number, to: number) => void;
};

export const minPage = 0;
export const defaultState = {
  page: minPage,
  groups: {
    [GroupType.Center]: [...Array.from(Array(centerBasketSize).keys())],
    [GroupType.Group1]: [],
    [GroupType.Group2]: [],
    [GroupType.Group3]: [],
    [GroupType.Group4]: [],
  },
};

const AppContext = createContext<AppContextProps>({
  state: defaultState,
  setState: () => {},
  goToHome: () => {},
  goToPrev: () => {},
  goToNext: () => {},
  moveGroup: () => {},
});

export default AppContext;
