import React, { useContext } from 'react';
import { useDrag } from 'react-dnd';
import styled, { css, ThemeContext } from 'styled-components';
import {
  BsFillCircleFill,
  BsFillSquareFill,
  BsFillDiamondFill,
  BsFillStarFill,
} from 'react-icons/bs';
import { lighten } from '@material-ui/core';
import { Theme } from 'styles/theme';
import { ColorType } from 'styles/colors';
import { GroupType } from 'App/context';

type ContainerProps = {
  size: number;
  color: string;
  isDragging: boolean;
  canDrag: boolean;
  showBorder: boolean;
};
const Container = styled.div<ContainerProps>`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3vw;
  height: 3vw;

  ${({ theme, size, color, isDragging, canDrag, showBorder }) => css`
    opacity: ${isDragging ? 0.5 : 1};

    svg {
      font-size: 0.9vw;
      transform: scale(${size});
      fill: ${color};
    }

    ${showBorder &&
    css`
      border: 2px solid ${(theme as Theme).palette[ColorType.Blue]};
    `}

    ${canDrag &&
    css`
      cursor: pointer;
      background: #fff;
      border-radius: 0.5rem;
      &:hover {
        svg {
          fill: ${lighten(color, 0.4)};
        }
      }
    `}
  `}
`;

export enum PolygonType {
  Circle,
  Square,
  Diamond,
  Star,
}

export enum PolygonSizeType {
  S = 1,
  M,
  L,
}

export enum ItemTypes {
  PolygonItem = 'polygonItem',
}

export type PolygonProps = {
  id: number;
  from?: GroupType;
  type: PolygonType;
  size: PolygonSizeType;
  color: ColorType;
  canDrag?: boolean;
  showBorder?: boolean;
};

export const polygonMap = new Map([
  [PolygonType.Circle, BsFillCircleFill],
  [PolygonType.Square, BsFillSquareFill],
  [PolygonType.Diamond, BsFillDiamondFill],
  [PolygonType.Star, BsFillStarFill],
]);

const Polygon: React.FC<PolygonProps> = ({
  id,
  from,
  type,
  size,
  color,
  canDrag = true,
  showBorder = false,
}) => {
  const Icon = polygonMap.get(type);
  const [{ isDragging }, drag] = useDrag(() => ({
    type: ItemTypes.PolygonItem,
    item: { from, polygonId: id },
    canDrag,
    collect: (monitor) => ({
      isDragging: !!monitor.isDragging(),
    }),
  }));
  const { palette } = useContext<Theme>(ThemeContext);
  const pickColor = palette[color];

  if (!Icon) {
    return null;
  }

  return (
    <Container
      ref={drag}
      isDragging={isDragging}
      size={size}
      color={pickColor}
      canDrag={canDrag}
      showBorder={showBorder}
    >
      <Icon />
    </Container>
  );
};

export default Polygon;
