import React from 'react';
import BasePage from 'components/BasePage';
import ButtonGroup from 'components/ButtonGroup';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
  Img,
  FlexDiv,
} from 'components/BaseStyles';
import HelpBox from 'components/HelpBox';
import CrowdImg from 'assets/images/crowd.gif';

const WhatIs: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>클러스터링 분석이란?</ContentTitle>
          <ExplainBox>
            클러스터링 기법은 여러 기준을 가지고 기계가 한 번에 정리하는
            기준이야.
            <br />
            주어진 데이터들을 특성에 따라 유사한 것끼리 묶어, 각 유형별 특징을
            분석하는 기법이지.
          </ExplainBox>
          <Content>
            <FlexDiv style={{ height: '100%' }}>
              <Img src={CrowdImg} alt="" />
            </FlexDiv>
          </Content>
          <HelpBox title="클러스터링 분석의 가장 대표적인 예" content="">
            <div>다양한 유형의 고객을 같은 유형끼리</div>
            <div>묶어주는 고객 세분화가 있어!</div>
          </HelpBox>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default WhatIs;
