import styled, { css } from 'styled-components';
import Button from 'components/Button';
import { Bounce } from 'styles/animations';
import defaultTheme, { Theme } from 'styles/theme';

export const PointText = styled.span`
  font-weight: 800;
  color: ${defaultTheme.palette.blue};
`;
export const Container = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-size: cover;
`;
export const Title = styled.div`
  font-size: 5rem;
  color: ${defaultTheme.palette.blue};
  font-family: 'Yj', sans-serif;
`;
export const SubTitle = styled.div`
  font-size: 1.2rem;
  font-family: 'Yspot', sans-serif;
  font-size: 2.2rem;
  text-align: center;
  margin: 1rem 0 5rem 0;
`;
export const BounceButton = styled(Button)`
  animation: ${Bounce} 0.7s infinite alternate;
`;

export const DivBase = styled.div`
  width: 80%;
  height: 80%;
  margin-top: 3rem;
  margin-bottom: 8.5rem;
`;
export const ContentTitle = styled.div`
  height: 3rem;
  font-family: 'Yspot';
  font-size: 2.4rem;
  text-align: center;
`;
export const Content = styled.div`
  width: 100%;
  height: calc(100% - 10rem);
  font-size: 1.2rem;
  text-align: center;
`;
export const ExplainBox = styled.div`
  font-size: 1.4rem;
  overflow: auto;
  min-width: 80%;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1.4rem 1.2rem;
  animation-duration: 2.5s;
  text-align: center;
`;
export const Img = styled.img`
  height: 90%;
`;
export const FlexDiv = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
`;

export const ConversationBox = styled.div`
  ${({ theme }) => {
    const Lg = (theme as Theme).palette.lightGray;
    const Mg = (theme as Theme).palette.midGray;
    return css`
      background-color: ${Lg};
      box-shadow: 5px 5px 7px ${Mg};
    `;
  }}
  border-radius: 1.5rem;
  overflow: auto;
  max-width: 45%;
  max-height: 100%;
  animation-duration: 2.5s;
  margin-left: 3rem;
`;
