import React from 'react';
import ButtonGroup from 'components/ButtonGroup';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import DiceImg from 'assets/images/dice.gif';

const General: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>다양한 특성을 고려한 종합분석</ContentTitle>
          <ExplainBox>
            크기, 색깔, 모양에 따른 분류는 한 가지 기준으로만 정리된 건데,
            <br />세 가지 기준을 동시에 고려하여 데이터를 정리하면 어떨까?
          </ExplainBox>
          <Content>
            <img style={{ height: '100%' }} src={DiceImg} alt="" />
          </Content>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default General;
