import React, { useState } from 'react';
import BasePage from 'components/BasePage';
import ButtonGroup from 'components/ButtonGroup';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
  FlexDiv,
  Img,
  ConversationBox,
} from 'components/BaseStyles';
import HelpBox from 'components/HelpBox';
import MessyImg from 'assets/images/messy.gif';
import ChatBot from 'assets/images/chat_robot.png';
import ChatBini from 'assets/images/chat_bini.png';
import ChatUI from 'components/ChatUI';

const Purpose: React.FC = () => {
  const [isOpenNextPage, setOpenNextPage] = useState(false);
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>클러스터링 분석의 목적</ContentTitle>
          <ExplainBox>
            그렇다면 왜 클러스터링 분석을 하는 것일까? <br />
            카테고리별로 개체를 잘 분류하면 관리 편의성과 효율성이 좋아지기
            때문이야!
          </ExplainBox>
          <Content>
            <FlexDiv>
              <Img src={MessyImg} alt="" />
              <ConversationBox>
                <ChatUI
                  users={[
                    { userId: 0, imgSrc: ChatBini },
                    { userId: 1, imgSrc: ChatBot, isFirstPersonView: true },
                  ]}
                  messages={[
                    {
                      id: 0,
                      userId: 0,
                      text:
                        '카테고리 분류를 잘 하면 어떤 경우에 관리가 편해질까?',
                    },
                    {
                      id: 1,
                      userId: 1,
                      text:
                        '물건들을 정리할 때 비슷한 물건끼리 정리해놓으면 나중에 물건을 찾을 때 쉽게 찾을 수 있겠지?',
                    },
                    {
                      id: 2,
                      userId: 1,
                      text:
                        '마찬가지로 만약 쇼핑몰에서 고객 정보를 비슷한 유형끼리 잘 분류해놓는다면, 고객에게 상품을 추천하거나 서비스를 제공할 때 비슷한 유형의 고객 정보를 활용하여 적절한 서비스를 제공할 수 있을 거야!',
                    },
                  ]}
                  animation
                  setOpenNextPage={setOpenNextPage}
                />
              </ConversationBox>
            </FlexDiv>
          </Content>
          <HelpBox title="클러스터링 분석의 장점" content="">
            <div>평균의 함정에 빠지지 않을 수 있도록 도와줘!</div>
            <div>평균의 함정이란 어떤 집단의 평균이</div>
            <div>집단에 속한 각 객체의 특성을 반영해주지</div>
            <div>못하는 현상을 말해.</div>
          </HelpBox>
        </DivBase>
        <ButtonGroup isOpenNextPage={isOpenNextPage} />
      </Container>
    </BasePage>
  );
};

export default Purpose;
