import React from 'react';
import BasePage from 'components/BasePage';
import ButtonGroup from 'components/ButtonGroup';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import HelpBox from 'components/HelpBox';
import KmeansVideo from 'assets/sounds/k_means_video.mp4';

const Understanding: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>클러스터링 기법의 이해</ContentTitle>
          <ExplainBox>
            가장 많이 쓰이는 클러스터링 기법은 K-means 알고리즘이야.
            <br />
            분류하고자 하는 그룹 K개의 점을 찍고 그 점을 기반으로 정리하는
            기술이지.
          </ExplainBox>
          <Content
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            <video loop autoPlay controls style={{ height: '90%' }}>
              <source src={KmeansVideo} type="video/mp4" />
              <track src="" kind="captions" label="captions" />
            </video>
          </Content>
          <HelpBox title="[심화] K-means 클러스터링이란?" content="">
            <div>&#34;관련성이 높은 데이터 사이의 거리는 가깝다&#34;가</div>
            <div>클러스터링 분석의 핵심사고야.</div>
            <div>거리가 가까운지를 알려면, 기준점이 필요하겠지?</div>
            <div>이 중심을 찾아주는 게 K-means 기법이야.</div>
            <div>컴퓨터가 수만 번 데이터들 간의 거리를 계산하여,</div>
            <div>가장 거리가 짧은 중심점의 위치를 찾아</div>
            <div> K의 개수만큼 그룹을 생성해주지.</div>
          </HelpBox>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default Understanding;
