import React from 'react';
import BasePage from 'components/BasePage';
import ButtonGroup from 'components/ButtonGroup';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import SmileImg from 'assets/images/smile.gif';

const UnderstandingComplete: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>데이터 정리 - 클러스터링 기법이해 완료</ContentTitle>
          <ExplainBox>
            정리하면, 클러스터링은 유사한 특성을 지닌 데이터를 묶는 데이터 분석
            방법 중 하나야!
            <br />
            클러스터링은 방대한 데이터에서 우리가 알지 못했던 집단의 특성을
            파악하는데 도움을 줘!
          </ExplainBox>
          <Content style={{ marginTop: '2.5rem' }}>
            <img src={SmileImg} alt="" style={{ height: '100%' }} />
          </Content>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default UnderstandingComplete;
